<template>
  <div class="goodsinfo-container">

    <transition
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <swiper :lunbotuList="lunbotu" :isfull="false"></swiper>
        </div>
      </div>
    </div>


    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">{{ goodsinfo.title }}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            <del>{{ goodsinfo.market_price }}</del>&nbsp;&nbsp;<span class="now_price">{{ goodsinfo.sell_price }}</span>
          </p>
          <p><numbox @getcount="getSelectedCount" :max="goodsinfo.stock_quantity"></numbox></p>
          <p>
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="danger" size="small" @click="addToShopCar"></mt-button>
            <!--    -->
            <!-- 1.   goodsinfo    numberbox  -->
            <!-- 2.  goodsinfo zhong  -->
            <!-- 3.   -->
            <!-- 4.      -->
          </p>
        </div>
      </div>
    </div>


    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>{{ goodsinfo.goods_no }}</p>
          <p>{{ goodsinfo.stock_quantity }}</p>
          <p>{{ goodsinfo.add_time | dateFormat }}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="goDesc(id)"></mt-button>
        <mt-button type="danger" size="large" plain @click="goComment(id)"></mt-button>
      </div>
    </div>


  </div>
</template>

<script>
// 
import swiper from "../subcomponents/swiper.vue";
//   
import numbox from "../subcomponents/goodsinfo_numbox.vue";

export default {
  data() {
    return {
      id: this.$route.params.id, //  id  data , 
      lunbotu: [], // 
      goodsinfo: {}, // 
      ballFlag: false, // 
      selectedCount: 1 //  1
    };
  },
  created() {
    this.getLunbotu();
    this.getGoodsInfo();
  },
  methods: {
    getLunbotu() {
      DataSrc.getLunbotu().then(result => {
        result.forEach(item => {
            item.img = item.src;
          });
        this.lunbotu = result
      });
    },
    getGoodsInfo() {
      // 
      DataSrc.goods_getinfo(this.id).then(result => {
        this.goodsinfo = result[0];
      });
    },
    goDesc(id) {
      //  
      this.$router.push({ name: "goodsdesc", params: { id } });
    },
    goComment(id) {
      //  
      this.$router.push({ name: "goodscomment", params: { id } });
    },
    addToShopCar() {
      // 
      this.ballFlag = !this.ballFlag;
      // { id:id, count: , price: selected: false  }
      //  store  car  
      var goodsinfo = {
        id: this.id,
        count: this.selectedCount,
        price: this.goodsinfo.sell_price,
        selected: true
      };
      //  store  mutations 
      this.$store.commit("addToCar", goodsinfo);
    },
    beforeEnter(el) {
      el.style.transform = "translate(0, 0)";
    },
    enter(el, done) {
      el.offsetWidth;

      // 
      // 1.          
      // 2.    
      // 3.      
      // 4.         y   x   
      // 5.       domObject.getBoundingClientRect()

      //  
      const ballPosition = this.$refs.ball.getBoundingClientRect();
      //   
      const badgePosition = document
        .getElementById("badge")
        .getBoundingClientRect();

      const xDist = badgePosition.left - ballPosition.left;
      const yDist = badgePosition.top - ballPosition.top;

      el.style.transform = `translate(${xDist}px, ${yDist}px)`;
      el.style.transition = "all 0.5s cubic-bezier(.4,-0.3,1,.68)";
      done();
    },
    afterEnter(el) {
      this.ballFlag = !this.ballFlag;
    },
    getSelectedCount(count) {
      //   data 
      this.selectedCount = count;
      console.log(" " + this.selectedCount);
    }
  },
  components: {
    swiper,
    numbox
  }
};
</script>

<style lang="scss" scoped>
.goodsinfo-container {
  background-color: #eee;
  overflow: hidden;

  .now_price {
    color: red;
    font-size: 16px;
    font-weight: bold;
  }

  .mui-card-footer {
    display: block;
    button {
      margin: 15px 0;
    }
  }

  .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    z-index: 99;
    top: 390px;
    left: 146px;
  }
}
</style>



