<template>
  <div class="photoinfo-container">
    <h3>{{ photoinfo.title }}</h3>
    <p class="subtitle">
      <span>{{ photoinfo.add_time | dateFormat }}</span>
      <span>{{ photoinfo.click }}</span>
    </p>

    <hr>

    <!--  -->
    <div class="thumbs">
      <img class="preview-img" v-for="(item, index) in list" :src="item.src" height="100" @click="$preview.open(index, list)" :key="item.src">
    </div>

    <!--  -->
    <div class="content" v-html="photoinfo.content"></div>

    <!--   -->
    <cmt-box :id="id"></cmt-box>
  </div>
</template>

<script>
// 1. 
import comment from "../subcomponents/comment.vue";

export default {
  data() {
    return {
      id: this.$route.params.id, //  Id
      photoinfo: {}, // 
      list: [] // 
    };
  },
  created() {
    this.getPhotoInfo();
    this.getThumbs();
  },
  methods: {
    getPhotoInfo() {
      // 
      DataSrc.getimageInfo(this.id).then(result => {
        this.photoinfo = result
      });
    },
    getThumbs() {
      // 
      DataSrc.getthumimages(this.id).then(result => {
        result.forEach(item => {
            item.w = 600;
            item.h = 400;
          });
        this.list = result
      });
    }
  },
  components: {
    //  
    "cmt-box": comment
  }
};
</script>

<style lang="scss" scoped>
.photoinfo-container {
  padding: 3px;
  h3 {
    color: #26a2ff;
    font-size: 15px;
    text-align: center;
    margin: 15px 0;
  }
  .subtitle {
    display: flex;
    justify-content: space-between;
    font-size: 13px;
  }

  .content {
    font-size: 13px;
    line-height: 30px;
  }

  .thumbs{
    img{
      margin: 10px;
      box-shadow: 0 0 8px #999;
    }
  }
}
</style>



