<template>
  <div class="cmt-container">
    <h3></h3>
    <hr>
    <textarea placeholder="BB120" maxlength="120" v-model="msg"></textarea>

    <mt-button type="primary" size="large" @click="postComment"></mt-button>

    <div class="cmt-list">
      <div class="cmt-item" v-for="(item, i) in comments" :key="item.add_time">
        <div class="cmt-title">
          {{ i+1 }}&nbsp;&nbsp;{{ item.user_name }}&nbsp;&nbsp;{{ item.add_time | dateFormat }}
        </div>
        <div class="cmt-body">
          {{ item.content === 'undefined' ? '': item.content }}
        </div>
      </div>

    </div>

    <mt-button type="danger" size="large" plain @click="getMore"></mt-button>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      pageIndex: 1, // 
      comments: [], // 
      msg: "" // 
    };
  },
  created() {
    this.getComments();
  },
  methods: {
    getComments() {
      // 
      DataSrc.getcomments(this.id, this.pageIndex).then(result => {
        this.comments = this.comments.concat(result);
      });
    },
    getMore() {
      // 
      this.pageIndex++;
      this.getComments();
    },
    postComment() {
      // 
      if (this.msg.trim().length === 0) {
        return Toast("");
      }

      // 
      // 1 URL
      // 2  { content: this.msg }
      // 3   { emulateJSON:true }
      DataSrc.postcomment({
        id:this.$route.params.id,
        content:this.msg.trim()
      }).then(result => {
        var cmt = {
              user_name: "",
              add_time: Date.now(),
              content: this.msg.trim()
            };
            this.comments.unshift(cmt);
            this.msg = "";
      });
    }
  },
  props: ["id"]
};
</script>

<style lang="scss" scoped>
.cmt-container {
  h3 {
    font-size: 18px;
  }
  textarea {
    font-size: 14px;
    height: 85px;
    margin: 0;
  }

  .cmt-list {
    margin: 5px 0;
    .cmt-item {
      font-size: 13px;
      .cmt-title {
        line-height: 30px;
        background-color: #ccc;
      }
      .cmt-body {
        line-height: 35px;
        text-indent: 2em;
      }
    }
  }
}
</style>



